@import url('https://fonts.googleapis.com/css2?family=Finger+Paint&display=swap');

@font-face {
    font-family: "ethnocentric";
    src: local("ethnocentric"), url(ethnocentric-rg.woff) format("woff");
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html,
body {
    font-family: 'ethnocentric', sans-serif !important;
}

.main {
    background: url('../images/bg.jpeg');
    min-height: 100vh;
    background-position: top right;
    background-size: cover;
    position: relative;
}

/* global */
img {
    max-width: 100%;
    width: 100%;
}

h1 {
    font-size: 4rem;
}

h3 {
    text-transform: uppercase;
    font-size: 1.5rem;
}

p {
    font-size: 1.2rem;
}

.btn {
    outline: 0;
    border: 4px solid #fff;
    border-radius: 30px;
    font-weight: bold;
    font-family: 'ethnocentric', sans-serif;
    text-transform: uppercase;
    background: none;
    color: #fff;
    padding: 5px 12px;
    font-size: 1.2rem;
    cursor: pointer;
}

.mint__btn {
    padding: 0.5rem 2.5rem;
    transition: 0.4s ease;
}

.mint__btn:hover {
    background: #ee85fd;
}

.buy__wrapper {
    display: flex;
    gap: 1.5rem;
    padding: 2rem 0;
}

.buy__wrapper p,
.buy__wrapper button {
    font-size: 2rem;
}

.buy__wrapper button {
    border-radius: 10px;
    padding: 0 20px;
    background: none;
    border: 2px solid #fff;
    color: #fff;
    cursor: pointer;
}

/* section */
.header__logo img {
    width: 400px;
    height: 400px;
    border-radius: 100%;
}

.header__logo {
    padding: 40px 0;
    display: flex;
    justify-content: center;
    /*position: absolute;*/
    /*top: 0;*/
    width: 100%;
}

.main__wrapper {
    max-width: 100%;
    width: 100%;
}

.container {
    max-width: 1320px;
    width: 100%;
    margin: 0 auto;
    /*grid-template-columns: 2fr 1fr;*/
    /*display: grid;*/
    min-height: calc(100vh - 480px);
    display: flex;
    justify-content: center;
    align-items: center;
}

.right__col {
    font-family: 'ethnocentric', sans-serif;
    color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    align-self: center;
    justify-self: flex-start;
    text-align: center;
}

.description {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px 0;
}

@media only screen and (max-width: 1440px) {
    .container {
        grid-template-columns: 2fr 1fr;
    }
}

@media only screen and (max-width: 1280px) {
    .container {
        grid-template-columns: 1fr 1fr;
    }
}

@media only screen and (max-width: 1024px) {
    .container {
        grid-template-columns: 1fr;
    }

    .right__col {
        justify-self: center;
    }

    .left__col: {
        display: none;
        position: absolute;
    }
}

@media only screen and (max-width: 600px) {
    .main {
        padding-bottom: 40px;
    }

    .container {
        min-height: calc(100vh - 480px - 40px);
    }

    .main__wrapper {
        grid-template-columns: 1fr;
    }

    .right__col {
        margin-top: 1rem;
        padding: 0 10px;
    }

    .err__msg {
        font-size: 1rem !important;
        text-align: center;
    }
}

@media only screen and (max-width: 320px) {
    .main {
        padding-bottom: 20px;
    }

    .container {
        min-height: calc(100vh - 480px - 20px);
    }

    .header__logo {
        padding-top: 20px;
        padding-bottom: 20px;
    }

    .container {
        padding-top: 160px;
        padding-bottom: 10px;
    }

    .right__col {
        margin-top: 1rem;
        padding: 0 10px;
    }

    .right__col h3 {
        font-size: 1.2rem;
    }

    .description p {
        font-size: 1rem;
    }
}
